import React, { useState, useEffect, useRef } from "react";
import emailjs from '@emailjs/browser';
import "./contact.scss";

const Contact = () => {
  const form = useRef();
  const [formData, setFormData] = useState({
    nombre: "",
    email: "",
    description: "",
  });

  const [enviado, setEnviado] = useState(false);
  const [enviando, setEnviando] = useState(false);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    setEnviando(true);
    emailjs.sendForm('service_hqyg4e6', 'template_cx1l5i9', form.current, '2C5GEQ90tS7vWjHtH')
      .then((result) => {
          console.log(result.text);
          setEnviando(false);
          setEnviado(true);
          setTimeout(() => {
            setEnviado(false);
          }, 3000);
      }, (error) => {
          console.log(error.text);
      });
  };

  useEffect(() => {
    if (enviado) {
      setFormData({
        nombre: "",
        email: "",
        description: "",
      });
    }
  }, [enviado]);

  return (
    <section id="contact">
      <div className="container-lg contact">
        <div className="section-header">
          <h4>Contacto</h4>
          <p>¿Nos tomamos un café?</p>
        </div>
        <div className="section-container">
          <div className="contact-form">
            <form onSubmit={handleSubmit} ref={form}>
              <div className="container-contact">
                <div className="contact-left">
                  <label htmlFor="nombre">Nombre</label>
                  <input
                    type="text"
                    id="nombre"
                    name="nombre"
                    value={formData.nombre}
                    onChange={handleInputChange}
                    required
                  />

                  <label htmlFor="email">Email</label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    required
                  />
                </div>
                <div className="contact-right">
                  <label htmlFor="description">Descripción</label>
                  <textarea
                    id="description"
                    name="description"
                    value={formData.description}
                    rows={6}
                    onChange={handleInputChange}
                    required
                  />
                </div>
              </div>
              <button
                className={`bt-submit ${enviando ? "enviando" : enviado ? "enviado" : ""}`}
                type="submit"
                disabled={enviado || enviando}
              >
                {enviando ? (
                  <div className="spinner"></div>
                ) : (
                  enviado ? "Enviado" : "Enviar"
                )}
              </button>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;
