import React from "react";
import LogoFooter from "../../assets/img/logofooter.svg";
import { Link } from "react-scroll";
import {
  SlSocialGithub,
  SlSocialLinkedin,
  SlSocialInstagram,
} from "react-icons/sl";

import "./footer.scss";

const Footer = () => {
  return (
    <footer>
      <div className="footer-container">
        <div className="footer-left">
          <img className="footer-logo" alt="logo" src={LogoFooter} />
          <ul className="footer-social-icons">
            <li>
              <a
                target="_blank"
                rel="noreferrer"
                title="LinkedIn"
                href="https://www.linkedin.com/in/ldanielgg/"
              >
                <SlSocialLinkedin />
              </a>
            </li>
            <li>
              <a
                target="_blank"
                rel="noreferrer"
                title="Github"
                href="https://github.com/TitoDevs"
              >
                <SlSocialGithub />
              </a>
            </li>
            <li>
              <a
                target="_blank"
                rel="noreferrer"
                title="Instagram"
                href="https://www.instagram.com/tito.dev/"
              >
                <SlSocialInstagram />
              </a>
            </li>
          </ul>
        </div>
        <div className="footer-right">
          <ul className="footer-nav">
            <li>
              <Link
                to="home"
                spy={true}
                smooth={true}
                offset={0}
                duration={500}
              >
                Home
              </Link>
            </li>
            <li>
              <Link
                to="about-me"
                spy={true}
                smooth={true}
                offset={-10}
                duration={500}
              >
                Sobre mí
              </Link>
            </li>
            <li>
              <Link
                to="experience"
                spy={true}
                smooth={true}
                offset={-10}
                duration={500}
              >
                Educación
              </Link>
            </li>
            <li>
              <Link
                to="projects"
                spy={true}
                smooth={true}
                offset={-5}
                duration={500}
              >
                Proyectos
              </Link>
            </li>
            <li>
              <Link
                to="contact"
                spy={true}
                smooth={true}
                offset={-5}
                duration={500}
              >
                Contacto
              </Link>
            </li>
          </ul>
        </div>
      </div>
      <div className="separator" />
      <div className="copyright-container">titodev © 2024</div>
    </footer>
  );
};

export default Footer;
